import {NgModule, ModuleWithProviders} from '@angular/core';
import {CommonModule} from '@angular/common';

import {AdaptBusyDirective} from './busy.directive';
import {AdaptBusyBackdropComponent} from './busy-backdrop.component';
import {AdaptBusyComponent} from './busy.component';
import {AdaptBusyConfig} from './busy-config';
import {AdaptBusyTplComponent} from './busy-tpl.component';
import {AdaptBusyHostDirective} from './busy-ad-host.directive';
import {AdaptInlineStandaloneDirective} from './busy-inline-standalone.directive';

@NgModule({
  imports: [
    CommonModule
  ],
  declarations: [
    AdaptBusyDirective,
    AdaptBusyComponent,
    AdaptBusyBackdropComponent,
    AdaptBusyTplComponent,
    AdaptBusyHostDirective,
    AdaptInlineStandaloneDirective
  ],
  exports: [AdaptBusyDirective, AdaptInlineStandaloneDirective],
  entryComponents: [
    AdaptBusyComponent,
    AdaptBusyBackdropComponent,
    AdaptBusyTplComponent
  ]
})
export class AdaptBusyModule {
  static forRoot(config?: AdaptBusyConfig): ModuleWithProviders<AdaptBusyModule> {

    return {
      ngModule: AdaptBusyModule,
      providers: [{provide: AdaptBusyConfig, useValue: config}]
    };
  }
}

export * from './busy-config';
export * from './busy-backdrop.component';
export * from './busy-ad-host.directive';
export * from './promise-tracker.service';
export * from './busy.component';
export * from './busy.service';
export * from './busy.directive';
export * from './busy-inline-standalone.directive';
